import { FabricObject } from '../../shared/fabric'
import { DropDownIcon, CircleIcon } from '../icon'
import { Table, Attribute, CanvasEvent, Id } from '../../../types'
import {
  DataTypeDate,
  DataTypeNumber,
  DataTypeString,
} from '../../../types/enum'
import { imageMap } from '../../shared/image'
import { isNumber } from '../../utils/verify'

interface NodeInput extends Table {
  x?: number
  y?: number
  width?: number
  borderColor?: string
  textColor?: string
  bgColor?: string
}
export class NodeLineage extends FabricObject {
  private ctx: CanvasRenderingContext2D
  public width = 260
  public height = 0
  public titleHeight = 30
  private titlePaddingX = 12
  private titlePaddingY = 8
  public borderColor = '#0C64EB'
  public textColor = '#0C64EB'
  public bgColor = '#D0E2FF'
  public attrHeight = 24
  private borderWidth = 1
  private datatypeIconWidth = 14
  public expandLeft = true
  public expandRight = true

  public x = 0
  public y = 0
  public id: Id
  public enName: string
  public cnName: string
  public type: string
  public attributes: Attribute[]

  constructor(ctx: CanvasRenderingContext2D, input: NodeInput) {
    super()
    this.ctx = ctx
    this.updateOptions(input)
  }

  private analysisOptions(input: NodeInput) {
    if (!input) return

    const {
      x = 0,
      y = 0,
      id,
      enName,
      cnName,
      type,
      attributes,
      borderColor,
      textColor,
      bgColor,
    } = input

    if (isNumber(x)) this.x = x
    if (isNumber(y)) this.y = y
    if (id) this.id = id
    if (enName) this.enName = enName
    if (cnName) this.cnName = cnName
    if (type) this.type = type
    if (attributes) this.attributes = attributes
    if (borderColor) this.borderColor = borderColor
    if (textColor) this.textColor = textColor
    if (bgColor) this.bgColor = bgColor
  }

  public updateOptions(input: NodeInput) {
    this.analysisOptions(input)
    this.calcBoundingBox()
  }

  public containsPoint(e: CanvasEvent, trigger = false) {
    const { x: ex, y: ey } = e
    const {
      x,
      y,
      width,
      attrHeight,
      titleHeight,
      expandLeft,
      attributes,
      children,
    } = this

    // 是否节点触发
    const height = expandLeft
      ? titleHeight + attrHeight * attributes.length
      : titleHeight
    const linesForNode = this.getImageLines({
      tl: { x, y },
      tr: { x: x + width, y },
      bl: { x, y: y + height },
      br: { x: x + width, y: y + height },
    })
    const xpointsNode = this.findCrossPoints(ex, ey, linesForNode)
    const isContainsNode = Boolean(xpointsNode % 2)
    if (!isContainsNode) return false

    // 是否子节点触发
    for (let i = 0; i < children.length; i++) {
      const element = children[i]
      const isContains = element.containsPoint?.(e, trigger)
      if (isContains) return this.id
    }

    const relativeH = ey - y
    // 是否节点标题触发
    // if (relativeH < titleHeight) {
    //   trigger && this.accordionLeft()
    //   return id
    // }

    // 属性触发
    const attrIndex = Math.floor((relativeH - titleHeight) / attrHeight)
    return attributes[attrIndex]?.id
  }

  public draw(input?: NodeInput) {
    this.updateOptions(input)

    this.children = []
    this.expandLeft && this.drawBody()
    this.drawTitle()
    this.attributes?.length && this.drawDropDownIcon()
    // this.drawCircleIcon()
  }

  private drawBody() {
    const {
      ctx,
      x,
      y,
      width,
      titleHeight,
      attrHeight,
      titlePaddingX,
      attributes,
      datatypeIconWidth,
    } = this

    ctx.save()
    // draw BOX
    ctx.strokeStyle = '#C1C7CD'
    ctx.fillStyle = '#ffffff'
    const boxHeight = titleHeight + attrHeight * attributes.length
    ctx.drawRoundedRect(
      x,
      y,
      width,
      boxHeight,
      4,
      4,
      4,
      4,
      true,
      true,
      false,
      false,
    )

    // draw attributes
    ctx.translate(x, y + titleHeight)
    ctx.fillStyle = '#161616'
    ctx.textBaseline = 'top'
    ctx.font = '14px PingFang SC'
    const maxWidth = width - titlePaddingX * 2
    for (let i = 0; i < attributes.length; i++) {
      const attr = attributes[i]

      // ctx.beginPath()
      ctx.moveTo(0, attrHeight * i)
      if (i) ctx.lineTo(width, attrHeight * i)
      const text = ctx.fittingString(attr.enName, maxWidth - datatypeIconWidth)
      this.drawDataType(
        attr.type,
        titlePaddingX,
        attrHeight * i + (attrHeight - 14) / 2,
      )
      ctx.fillText(
        text,
        titlePaddingX + datatypeIconWidth,
        attrHeight * i + (attrHeight - 14) / 2,
      )
    }
    ctx.stroke()
    ctx.restore()
  }

  private drawDataType(
    datatype: DataTypeDate | DataTypeNumber | DataTypeString,
    x: number,
    y: number,
  ) {
    const datatypeUpper = datatype?.replace(/\(.*\)/g, '')?.toLocaleUpperCase()
    let img
    if (datatypeUpper in DataTypeDate) {
      img = imageMap.get('date')
    } else if (datatypeUpper in DataTypeNumber) {
      img = imageMap.get('number')
    } else if (datatypeUpper in DataTypeString) {
      img = imageMap.get('string')
    }
    if (!img) return false
    const { ctx } = this
    ctx.drawImage(img, x - 6, y - 2)
  }

  private drawTitle() {
    const {
      ctx,
      x,
      y,
      width,
      titleHeight,
      borderColor,
      bgColor,
      textColor,
      enName,
      titlePaddingX,
      titlePaddingY,
      expandLeft,
      attributes,
    } = this
    ctx.save()
    ctx.strokeStyle = borderColor
    ctx.fillStyle = bgColor
    const bottomRoundedRatio = expandLeft && attributes.length ? 0 : 4
    ctx.drawRoundedRect(
      x,
      y,
      width,
      titleHeight,
      4,
      4,
      bottomRoundedRatio,
      bottomRoundedRatio,
      true,
      true,
      false,
      false,
    )
    // ctx.fillRect(x, y, width, titleHeight)
    // ctx.strokeRect(x, y, width, titleHeight)
    ctx.textBaseline = 'top'
    ctx.font = '14px PingFang SC'
    ctx.fillStyle = textColor
    const maxWidth = width - titlePaddingX * 2 - 4 // -4是为了左边留出稍大的位置给icon
    const titleText = ctx.fittingString(enName, maxWidth)
    ctx.fillText(titleText, x + titlePaddingX + 4, y + titlePaddingY)
    ctx.restore()
  }

  private drawCircleIcon() {
    const {
      ctx,
      x,
      y,
      width,
      titleHeight,
      borderColor,
      textColor,
      expandRight,
    } = this
    const circleX = x + width
    const circleY = y + titleHeight / 2
    const circleIcon = new CircleIcon(ctx, {
      x: circleX,
      y: circleY,
      minus: expandRight,
      borderColor,
      textColor,
      tiggerFunc: this.accordionRight.bind(this),
    })
    circleIcon.draw()
    this.children.push(circleIcon)
  }

  private drawDropDownIcon() {
    const { ctx, x, y, titleHeight, titlePaddingX, expandLeft } = this
    const arrowX = x + titlePaddingX / 2 + 2
    const arrowY = y + titleHeight / 2
    const dropDownIcon = new DropDownIcon(ctx, {
      x: arrowX,
      y: arrowY,
      rotate: expandLeft ? 0 : 180,
      tiggerFunc: this.accordionLeft.bind(this),
    })
    dropDownIcon.draw()
    this.children.push(dropDownIcon)
  }

  public accordionLeft() {
    this.calcBoundingBox()
    return (this.expandLeft = !this.expandLeft)
  }

  public accordionRight() {
    return (this.expandRight = !this.expandRight)
  }

  private calcBoundingBox() {
    const { x, y, width, attrHeight, expandLeft, titleHeight, attributes } =
      this
    const height = (this.height = expandLeft
      ? titleHeight + attrHeight * attributes.length
      : titleHeight)
    this.boundingBox = [
      [x, y],
      [x + width, y],
      [x, y + height],
      [x + width, y + height],
    ]
  }

  public getLinePosition(attributeId?: Id) {
    const { x, y, titleHeight, expandLeft, attributes, attrHeight, id } = this
    const position = {
      x,
      y,
    }
    if (expandLeft && attributeId !== id) {
      const attributeIndex = attributes.findIndex(
        (attr) => attr.id === attributeId,
      )
      position.y = y + titleHeight + attrHeight * (attributeIndex + 0.5)
    } else {
      position.y = y + titleHeight / 2
    }
    return position
  }
}
