import { FabricObject } from '../../shared/fabric'

interface Options {
  x: number
  y: number
  width?: number
  height?: number
  rotate?: number
  color?: string
  tiggerFunc?: () => void
}

export class DropDownIcon extends FabricObject {
  private ctx: CanvasRenderingContext2D
  private x = 0
  private y = 0
  private width = 8
  private height = 5
  private color = '#919EAB'
  private rotate = 0
  private tiggerFunc: () => void

  constructor(ctx: CanvasRenderingContext2D, options: Options) {
    super()
    this.ctx = ctx
    this.analysisOptions(options)
  }

  private analysisOptions(options) {
    Object.assign(this, options)
    // this.x = options.x
    // this.y = options.y
    // this.color = options.color
    // this.rotate = options.rotate
  }

  public draw() {
    const { ctx, x, y, width, height, color, rotate } = this
    ctx.save()
    ctx.beginPath()
    ctx.fillStyle = color
    ctx.translate(x, y)
    rotate && ctx.rotate((rotate * Math.PI) / 180)
    ctx.moveTo(-width / 2, -height / 2)
    ctx.lineTo(0, height / 2)
    ctx.lineTo(width / 2, -height / 2)
    ctx.fill()
    ctx.restore()
  }

  public containsPoint(e: MouseEvent, trigger = false) {
    const { width, height, x, y } = this
    const { x: ex, y: ey } = e
    const lines = this.getImageLines({
      tl: { x: x - width / 2, y: y - height / 2 },
      tr: { x: x + width / 2, y: y - height / 2 },
      bl: { x: x - width / 2, y: y + height / 2 },
      br: { x: x + width / 2, y: y + height / 2 },
    })

    const xpoints = this.findCrossPoints(ex, ey, lines)
    const isContains = Boolean(xpoints % 2)

    if (isContains && trigger) this.tiggerFunc?.()
    return isContains
  }
}
