import { FabricObject } from '../../shared/fabric'

interface Options {
  x: number
  y: number
  minus: boolean
  borderColor: string
  textColor: string
  tiggerFunc?: () => void
}

export class CircleIcon extends FabricObject {
  private ctx: CanvasRenderingContext2D
  private x = 0
  private y = 0
  private circleRadius = 8
  private minus = false
  private borderColor: string
  private textColor: string
  private tiggerFunc: () => void

  constructor(ctx: CanvasRenderingContext2D, options: Options) {
    super()
    this.ctx = ctx
    this.analysisOptions(options)
  }

  private analysisOptions(options) {
    this.x = options.x
    this.y = options.y
    this.minus = options.minus
    this.borderColor = options.borderColor
    this.textColor = options.textColor
    this.tiggerFunc = options.tiggerFunc
  }

  public draw() {
    const { ctx, x, y, borderColor, textColor, minus, circleRadius } = this
    ctx.save()
    ctx.beginPath()
    ctx.strokeStyle = borderColor
    ctx.fillStyle = '#ffffff'
    ctx.arc(x, y, circleRadius, 0, 260)
    ctx.fill()
    ctx.stroke()

    ctx.beginPath()
    ctx.fillStyle = textColor
    ctx.lineWidth = 2
    ctx.moveTo(x - 4, y)
    ctx.lineTo(x + 4, y)
    if (!minus) {
      ctx.moveTo(x, y - 4)
      ctx.lineTo(x, y + 4)
    }
    ctx.stroke()
    ctx.restore()
  }

  public containsPoint(e: MouseEvent, trigger = false) {
    const { circleRadius, x, y } = this
    const { x: ex, y: ey } = e
    const lines = this.getImageLines({
      tl: { x: x - circleRadius, y: y - circleRadius },
      tr: { x: x + circleRadius, y: y - circleRadius },
      bl: { x: x - circleRadius, y: y + circleRadius },
      br: { x: x + circleRadius, y: y + circleRadius },
    })

    const xpoints = this.findCrossPoints(ex, ey, lines)
    const isContains = Boolean(xpoints % 2)

    if (isContains && trigger) this.tiggerFunc?.()
    return isContains
  }
}
